:host{display:block}h2{font-size:3.5em;line-height:1.3;display:flex;justify-content:center;text-align:center;flex-direction:column;margin:60px 0 80px;align-items:center;min-height:140px}h2.correct{background:rgba(255,255,255,0.5);color:#70193A;animation:pulsate 0.8s infinite}.video,.image,.audio{margin-bottom:20px;position:relative;background:rgba(255,255,255,0.2)}.video:before,.image:before,.audio:before{content:'';display:block;padding-top:56.25%}.video .solution-bar,.image .solution-bar,.audio .solution-bar{position:absolute;top:0;left:0;width:100%;background:#70193A;padding:10px;color:#fff}.video .solution-bar h3,.image .solution-bar h3,.audio .solution-bar h3{display:inline-block;vertical-align:middle;margin:0 20px 0 0;line-height:45px}.video .solution-bar .btn,.image .solution-bar .btn,.audio .solution-bar .btn{vertical-align:middle}.image{background:no-repeat center center;background-size:cover}video{position:absolute;left:0;top:0;width:100%;height:100%;display:block;object-fit:initial}audio{position:absolute;left:0;top:0;width:100%;height:100%;display:block;object-fit:initial}.grid{border:solid #ddd;border-width:1px 0 0 1px;font-size:2em;height:50vh}.pattern{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;width:50%;float:left;height:50%;border:solid #ddd;border-width:0 1px 1px 0;color:rgba(255,255,255,0.5);padding:0 20px}.pattern.available{cursor:pointer;transition:background 0.3s;color:#fff;background:rgba(255,255,255,0.1)}.pattern.available:hover{background:rgba(255,255,255,0.3)}@keyframes pulsate{from{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.05, 1.05, 1.05)}to{transform:scale3d(1, 1, 1)}}
